import uniList from '../../components/uni-list/uni-list.vue'
import uniItem from '../../components/uni-list-item/uni-list-item.vue'
export default {
	data(){
		return{
			avatarUrl:'',
			name: '',
			isVip: false
		}
	},
	onLoad() {
		uni.getUserInfo({
			success:res=>{
				this.avatarUrl = res.userInfo.avatarUrl
				this.name = res.userInfo.nickName
				console.log(res.userInfo)
			}
		})
	},
	methods:{
		onBeVip(){
			uni.navigateTo({
				url: '../bevip/bevip',
				success: res => {
					console.log('success')
				}
			});
		},
		onOrderList(){
			uni.navigateTo({
				url: '../orderlist/orderlist'
			});
		},
		onMyWallet(){
			uni.navigateTo({
				url: '../mywallet/mywallet'
			});
		},
		onAddressList(){
			uni.navigateTo({
				url: '../addresslist/addresslist'
			});
		},
		onBusinessIn(){
			uni.navigateTo({
				url: 'business-in'
			})
		},
		onFriends(){
			uni.navigateTo({
				url: 'my-friends'
			})
		}
	},
	components: {
		uniList,
		uniItem
	},
}
